import Koa from 'koa'
import config from './config'
import serverInit from './middleware/server'  // 
import ctxResponse from './middleware/response'   // 
import headerGenerator from './middleware/http'   // 
import bodyParser from './middleware/bodyparser'  // 
import validator from './middleware/validator'  // 
import authority from './middleware/authority'  // 
import session from './middleware/session' // session
import routerFrame from './router'  // 

const app = new Koa()

app.use(serverInit())
app.use(ctxResponse())
app.use(headerGenerator())
app.use(bodyParser())
app.use(validator())
app.use(authority)
app.use(session(app))
app.use(routerFrame.routes()).use(routerFrame.allowedMethods())

app.listen(3002, () => {})
